import Link from 'next/link'
import { notFound } from 'next/navigation'
import { prisma } from '@/lib/db/prisma'
import { Button } from '@/components/ui/Button'
import { Card } from '@/components/ui/Card'
import { DeleteProjectButton } from '@/components/projects/DeleteProjectButton'

export default async function ProjectDetailPage({
  params,
}: {
  params: Promise<{ id: string }>
}) {
  const project = await prisma.project.findUnique({
    where: { id: (await params).id, deletedAt: null },
    include: {
      formats: {
        where: { deletedAt: null },
      },
      savedScripts: {
        where: { deletedAt: null },
        orderBy: { savedAt: 'desc' },
        take: 5,
      },
      _count: {
        select: {
          savedScripts: true,
        },
      },
    },
  })

  if (!project) {
    notFound()
  }

  const examples = JSON.parse(project.examples) as string[]

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      <div className="flex justify-between items-start mb-8">
        <div>
          <h1 className="text-3xl font-bold text-neutral-900 mb-2">
            {project.name}
          </h1>
          <p className="text-neutral-600">{project.description}</p>
        </div>
        <div className="flex gap-3">
          <Link href={`/projects/${project.id}/generate`}>
            <Button>Generate Scripts</Button>
          </Link>
          <Link href={`/projects/${project.id}/edit`}>
            <Button variant="secondary">Edit</Button>
          </Link>
          <DeleteProjectButton projectId={project.id} />
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6 mb-8">
        <Card>
          <h3 className="text-sm font-medium text-neutral-500 mb-1">
            Target Audience
          </h3>
          <p className="text-neutral-900">{project.targetAudience}</p>
        </Card>

        <Card>
          <h3 className="text-sm font-medium text-neutral-500 mb-1">Tone</h3>
          <p className="text-neutral-900">{project.tone}</p>
        </Card>

        <Card>
          <h3 className="text-sm font-medium text-neutral-500 mb-1">
            Saved Scripts
          </h3>
          <p className="text-2xl font-bold text-primary-600">
            {project._count.savedScripts}
          </p>
        </Card>
      </div>

      {examples.length > 0 && (
        <Card className="mb-8">
          <h2 className="text-xl font-semibold text-neutral-900 mb-4">
            Example Scripts
          </h2>
          <div className="space-y-4">
            {examples.map((example, idx) => (
              <div
                key={idx}
                className="p-4 bg-neutral-50 rounded-md text-sm text-neutral-700"
              >
                {example}
              </div>
            ))}
          </div>
        </Card>
      )}

      {project.savedScripts.length > 0 && (
        <div>
          <div className="flex justify-between items-center mb-4">
            <h2 className="text-xl font-semibold text-neutral-900">
              Recent Scripts
            </h2>
            <Link
              href={`/library?project=${project.id}`}
              className="text-sm text-primary-600 hover:text-primary-700 font-medium"
            >
              View all â†’
            </Link>
          </div>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {project.savedScripts.map((script) => (
              <Link key={script.id} href={`/library/${script.id}`}>
                <Card className="hover:border-primary-300 transition-colors cursor-pointer">
                  <h3 className="font-medium text-neutral-900 mb-2">
                    {script.title}
                  </h3>
                  <p className="text-sm text-neutral-600 line-clamp-2">
                    {script.hook}
                  </p>
                </Card>
              </Link>
            ))}
          </div>
        </div>
      )}
    </div>
  )
}
